import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-convenio-calusacd2-create',
  templateUrl: 'convenio-calusacd2-create.html',
})
export class ConvenioCalusacd2CreatePage {

  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;

  public tipo               : any;
  public rubro                 : any;
  public idtipounidad2		  :any=[];
  public idunidadacademica2		  :any=[];
  public tipounidad		     : any;
  public unidadacademica		     : any;
  public jornada		     : any;
  public idjornada2		  :any=[];
  public idperiodo		  :any;
  public ididioma                 : any;
  public idtipocurso                 : any;
  public idtipogrupo                 : any;
  public idpago2		  :any=[];
  public idperiodo2		  :any=[];
  public vgrupo  :any=[];
  public vgrupo2  :any=[];
  public vjornada  :any=[];

  public vpago  :any=[];
  public tipopago		     : any;
  public codigounidad                 : any;
  tiposus: any=[];

  public ano                 : any;
  public monto                 : any;
  public codigonn                 : any;


  public vvalidanivel		  :any=[];
  public sinivel		  :any='0';
  cuiv:boolean=false;
  public idprofesor2		  :any=[];
  public profesor		     : any;
  public vprofesor  :any=[];
  carnev:boolean=false;
  carnev2:boolean=false;

  vvcarne:any=''
  public nivel                 : any;
  cui:any='';
  private _ID                  : String;
  public pageTitle           	: string;
  comprobantenivel:boolean=false;

  constructor( public storage: Storage,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {
     // Define form validation - VERY basic!
     this.form = this._FB.group({
       nombre     : ['', Validators.required],
       monto    : ['', Validators.required],
       profesor : ['', Validators.required],
       idperiodo : ['', Validators.required],

 tipo     : ['', Validators.required],
       jornada     : ['', Validators.required],
       tipounidad     : ['', Validators.required],
       horario     : ['', Validators.required],
       tipopago     : ['', Validators.required],
       unidadacademica     : ['', Validators.required],

     });

     this.pageTitle='Detalle de estudiantes'

     this.authService.getReg2b('todos','tipounidad3s').then((result) => {
       this.vgrupo=result;
     }, (err) => {
     this.msj.showError3('Error',err.error);
   });




  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }

  onSelectChange2(selectedValue: any) {
    this.idunidadacademica2=selectedValue.split(',');

    this.vpago=[]
    this.vjornada=[]
    this.idjornada2=[]
    this.idpago2=[]


    this.rubro=this.idunidadacademica2[2]
    this.ididioma=this.idunidadacademica2[3]
    this.idtipogrupo=this.idunidadacademica2[4]
    this.idtipocurso=this.idunidadacademica2[5]

   if(  this.tipounidad=='')
   {}
   else
   {
            this.codigounidad=this.idunidadacademica2[2]








            this.authService.getReg2b(this.idtipounidad2[0]+'/'+ this.idunidadacademica2[0]+'/todosver','unidadperiodo3s').then((result) => {
            if(result)
            {
              this.vperiodo=result;


            }
            }, (err) => {
            this.msj.showError3('Error',err.error);
            });

   }
  }



  onSelectChange4(selectedValue: any) {
    this.idjornada2=selectedValue.split(',');

    this.vpago=[]

    this.idpago2=[]



  this.authService.getReg2b( this.idtipounidad2[0]+'/'+ this.idunidadacademica2[0] + '/'+ this.idperiodo2[0]  + '/' + this.tipo   +'/'+  this.idjornada2[0]+'/1','unidadplan3s').then((result) => {
    this.vpago=result;
  }, (err) => {
  this.msj.showError3('Error',err.error);
  });


  }
  public vhorario  :any=[];
  public idhorario2		  :any=[];

  public horario		     : any;

  public vperiodo  :any=[];

  onSelectChange3a(selectedValue: any) {

    this.idperiodo2=selectedValue.split(',');
    this.ano=this.idperiodo2[2]


    this.authService.getReg2b( this.idtipounidad2[0]+'/'+ this.idunidadacademica2[0] + '/'+this.idperiodo2[0] + '/1'+'/jornadas2a','unidadplan3s').then((result) => {
      this.vjornada=result;
    }, (err) => {
    this.msj.showError3('Error',err.error);
    });
  }
  onSelectChange3(selectedValue: any) {
   this.idpago2=selectedValue.split(',');
    this.monto=this.idpago2[3]
  this.codigonn=Number(this.idpago2[2])-1
    this.sinivel='0';
    this.nivel=this.idpago2[4]




    this.authService.getReg2b( this.idtipounidad2[0]+'/'+ this.idunidadacademica2[0] + '/'+this.idperiodo2[0]   + '/' +this.idjornada2[0] + '°' + this.nivel +'/horarios2a','unidadplan3s').then((result) => {
      this.vhorario=result;
    }, (err) => {
    this.msj.showError3('Error',err.error);
    });




  if(this.codigonn>0)
  {
    this.carnev=true
    this.carnev2=true
    this.vvcarne='Obligatorio'


    this.comprobantenivel=true;

  }
  else
  {
    this.comprobantenivel=false
    this.sinivel='1';
    this.carnev=true;
    this.carnev2=false;
    this.comprobantenivel=false;
    this.vvcarne='Si no tiene carné, no ingrese ninguna información'

    this.codigonn=0;
  }




  }


  onSelectChange(selectedValue: any) {

    this.idtipounidad2=selectedValue.split(',');
    this.vpago=[]
    this.vjornada=[]
    this.idjornada2=[]
    this.idpago2=[]



    if(this.idtipounidad2[2]=='Inactivo')    {
      this.msj.showError3('Precaución',this.idtipounidad2[3]);
      this.idtipounidad2=[]
      this.tipounidad=''
      return;

    }
    else
    {
      if(  this.tipounidad=='')
      {}
      else
      {



            // if(this.idtipounidad2[0]=='5b97f1bceb1dab0ab0368cc6')    {
                  this.authService.getReg2b(this.idtipounidad2[0] + '/2/'+ this.tipo,'unidadacademica3s').then((result) => {
                    this.vgrupo2=result;
                  }, (err) => {
                  this.msj.showError3('Error',err.error);
                });

            /*  }
              else
              {

                this.msj.showError3('Mensaje','La asignación para: ' +  this.idtipounidad2[1]+' se realizará en la Sede del Centro Universitario');

              }
              */
  }}
  }




  onSelectChange5(selectedValue: any) {    this.idhorario2=selectedValue.split(',');




  this.authService.getReg2b(  this.idtipounidad2[0]+'/'+ this.idunidadacademica2[0] + '/'+this.idperiodo2[0]  + '/' + this.nivel  + '°' +  this.idhorario2[0]  + '°' + this.idjornada2[0]+'/profesores2a','unidadplan3s').then((result) => {

    this.vprofesor=result;
  }, (err) => {
  this.msj.showError3('Error',err.error);
  });




}

onSelectChange7(selectedValue: any) {    this.idprofesor2=selectedValue.split(',');
}


  ionViewDidLoad() : void
  {
    if(this.navParams.get("record")==null)
    {


      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       this.nombre		=	this.navParams.data.record.nombre;
       this.monto		=	this.navParams.data.record.monto;

       this.rubro		=	this.navParams.data.record.rubro;
       this.cui	=	this.navParams.data.record.cui;




       this.authService.getReg2b('todos','tipounidad3s').then((result) => {
        this.vgrupo=result;
        this.tipounidad  	=	this.navParams.data.record.idtipounidad.id+','+this.navParams.data.record.idtipounidad.nombre+','+this.navParams.data.record.idtipounidad.codigo




        this.authService.getReg2b(this.navParams.data.record.idtipounidad.id+ '/2/'+ this.tipo,'unidadacademica3s').then((result) => {
          this.vgrupo2=result;
          this.unidadacademica		=	this.navParams.data.record.idtipounidad.id+','+this.navParams.data.record.idtipounidad.nombre+','+this.navParams.data.record.idtipounidad.codigo  +','+this.navParams.data.record.ididioma+','+this.navParams.data.record.idtipocurso+','+this.navParams.data.record.idtipogrupo
        }, (err) => {
        this.msj.showError3('Error',err.error);
      });



      }, (err) => {
      this.msj.showError3('Error',err.error);
    });




   this.ididioma	=	this.navParams.data.record.ididioma;
   this.tipopago	=	this.navParams.data.record.tipopago;
   this.jornada	=	this.navParams.data.record.jornada;
   this.nivel	=	this.navParams.data.record.nivel;
   this.horario	=	this.navParams.data.record.horario;




       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
  }



  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
     let   options       : any	     = {
      idtipounidad : {id:this.idtipounidad2[0],nombre:this.idtipounidad2[1]},
      idunidadacademica : {id:this.idunidadacademica2[0],nombre:this.idunidadacademica2[1],codigo:this.codigounidad},

       idempresa:this.navParams.data.idempresa,

     comentario : this.form.controls['nombre'].value,
     monto: this.form.controls['monto'].value,
   estado:'Creada',
     ididioma:this.ididioma,
     tipopago: this.idpago2[0],
     jornada: this.idjornada2[0],
     profesor: this.idprofesor2[0],
     nivel: this.nivel,
     horario: this.idhorario2[0],
     tipogrupo:this.idtipogrupo,
     tipocurso:this.idtipocurso,
     idperiodo:this.idperiodo,
      tipo:this.tipo,
     bitacora:{idempresa:this.navParams.data.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' market grupos '}
    };


     if(this.navParams.get("record")==null)
     {
          if(options){
            this.authService.createReg('',options,'conveniocalusacd2s').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'conveniocalusacd2s').then((result) => {
           if (!this.form.valid) { return; }
        //   console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }

}



